.dialogsBlock {
  display: flex;
  z-index: 99;
  height: max-content;
  width: 40vw;
}
.dialogsItems {
  width: 200px;
  background-color: rgb(255, 255, 255);
  border-right: 1px solid rgba(0, 0, 0, 0.131);
}
.messagesBlock{
  width: 40vw;
}

.dialog a{
  display: block;
  margin: 10px ;
}

.dialogsBlock .You {
  background-color: rgb(183, 183, 183);
  border-radius: 10px 10px 10px 0px;
  margin: 10px 0.3em;
  padding: 5px;
  max-width: max-content;
}
.dialogsBlock .I {
  background-color: rgb(255, 204, 0);
  border-radius: 10px 10px 0px 10px;
  margin: 10px;
  padding: 5px;
  max-width: max-content;
  min-width: max-content;
  /*text-align: right; */
  max-width: 8em; /* –±–ª–æ–∫ –∑–∞–Ω–∏–º–∞–µ—Ç —à–∏—Ä–∏–Ω—É —Ä–æ–¥–∏—Ç–µ–ª—è, max-width –µ—ë –æ–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ—Ç */
  margin-left: auto;
  margin-right: 0.3em;
}

.blockMessage {
  background-color: rgb(255, 255, 255);
  padding: 10px;
  height: 80vh;
}
.myMessag {
  background-color: rgb(255, 204, 0);
  padding: 10px 10px;
  border-radius: 0px 0px 5px 0px;
  display: grid;
  grid-template-areas: "a b";
  grid-template-columns: 10fr 3fr;
  padding-left: 40px;
}
.myMessag input{
  height: 30px;
  border: 0px;
  border-bottom: 1px solid black;
  margin-bottom: 5px;
	background-color: rgba(232, 232, 232, 0.472);

}
.myMessag input:hover{
	background-color: rgba(232, 232, 232, 0.878);
	color: #000000;
  text-decoration: none;
}
.myMessagButton button{
  margin: 0px  15px;
  height: 35px;
  width: 70px;
  border: 0px;
  background-color: rgba(255, 255, 255, 0.845);
  -webkit-box-shadow: 6px 0px 8px -2px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 6px 0px 8px -2px rgba(0, 0, 0, 0.2);
  box-shadow: 6px 1px 8px -2px rgba(0, 0, 0, 0.2);
  grid-area: b;
}
